import { HashRouter, Routes, Route } from 'react-router-dom';

import Home from './pages/Home';
import Promova from './pages/Promova';


function RoutesApp() {
    return (

        <HashRouter  basename='/'>
            <Routes>

                <Route path="/" element={<Home />} />
                <Route path="promovaonline" element={<Promova/>} />

            </Routes>
        </HashRouter>


    )

}

export default RoutesApp;